
.App {
  text-align: center;
  font-family: sans-serif;
  padding: 2rem;
  background: #1DB954;
  color: white;
  min-height: 100vh;
  /* transition: background-color 3s ease; */
}

img {
  /* border-radius: 20px; */
  box-shadow: 0 10px 20px rgba(0,0,0,0,.4);
  max-width: 100%;
  max-height: 100%;
}

a {
  color: white;
  text-decoration: underline;
}

.fade-container {
  transition: opacity 0.8s ease, transform 0.8s ease;
}

.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.fade-out {
  opacity: 0;
  transform: translateY(20px);
}

.album-img {
  border-radius: 1rem;
  box-shadow: 0 4px 20px rgba(0,0,0,0,0.4);
  transition: all 0.8s ease;
}

.square {
    height: 75px;
    width: 75px;
}

.top-tracks-section {
  max-width: 800px;
  margin: 4rem auto;
  padding: 1rem;
  color: #f0f0f0;
  font-family: 'Segoe UI', sans-serif;
}

.top-tracks-section h2 {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 1.8rem;
}

.top-tracks-table {
  width: 100%;
  border-collapse: collapse;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  overflow: hidden;
}

.top-tracks-table thead {
  background-color: rgba(255, 255, 255, 0.1);
}

.top-tracks-table th,
.top-tracks-table td {
  padding: 12px 16px;
  text-align: left;
}

.top-tracks-table tbody tr {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  transition: background 0.3s;
}

.top-tracks-table tbody tr:hover {
  background-color: rgba(255, 255, 255, 0.07);
}

.track-cell {
  display: flex;
  align-items: center;
  gap: 12px;
}

.track-cell img {
  width: 40px;
  height: 40px;
  border-radius: 6px;
  object-fit: cover;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

@media (max-width: 600px) {
  .top-tracks-table th:nth-child(1),
  .top-tracks-table td:nth-child(1) {
    display: none; /* hide index on small screens */
  }
}

.tab-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.tab-buttons button {
  padding: 0.5rem 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  border: none;
  color: #fff;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.3s;
}

.tab-buttons button:hover {
  background: rgba(255, 255, 255, 0.15);
}

.tab-buttons .active {
  background: rgba(255, 255, 255, 0.2);
  font-weight: 600;
}

.table-wrapper {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
  padding-bottom: 2rem;
}

.disclaimer {
  font-size: 0.9rem;
  opacity: 1;
  text-align: center;
  margin-top: 1rem;
}


